* {
    padding: 0;
    margin: 0;
}

li {
    list-style: none;
}


  @import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


.wrap {
    width: 1420px;
    height: 4300px;
    margin: 0 auto;
}

header {
    height: 80px;
    display: flex;
    line-height: 80px;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;

    .logo {
        width: 220px;
        height: 80px;
        text-align: center;
        img {
            height: 80px;
        }

    }
    .lnb {
        width: 740px;
        height: 80px;
        display: flex;
        justify-content: space-around;
    }

    .gnb {
        width: 460px;
        display: flex;
        justify-content: flex-end;
        .search-container {
            position: relative;
            display: inline-block;
        }
        
        .search {
            width: 160px;
            height: 25px;
            background-color: #FD8234;
            border-radius: 15px;
            margin-top: 30px;
            margin-right: 22px;
            border: none;
            outline: none;
        }
        .search-button {
            position: absolute;
            top: 2px;
            right: 22px;
            height: 100%;
            background-color: transparent;
            border: none;
            outline: none;
            cursor: pointer;
            padding: 0 10px;
        }
        .fas.fa-search {
            color: white; 
        }
        .search::-webkit-search-cancel-button {
            display: none;
        }
        .login{
            width: 25px;
            height: 25px;
            margin-right: 20px;
        }
        .mypage{
            width: 25px;
            height: 25px;
            margin-right: 20px;
        }
        .cart{
            width: 25px;
            height: 25px;
            margin-right: 25px;
        }
    }
}



